body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: var(--qm-color);
}
.btn {
  color: var(--qm-color);
}
#quote-box {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  margin-top: 150px;
  color: var(--qm-color);
}
#text {
  font-family: Monserrat;
  font-size: 20px;
  color: var(--qm-color);
}
#quote-author {
  text-align: right;
  font-size: 14px;
  color: var(--qm-color);
}
#quote-buttons {
  text-align: center;
}
#quote-footer {
  padding: 30px;
  text-align: center;
  color: white;
}

.fa-bootstrap {
  color: rgb(141, 133, 255);
  font-size: 16px;
}

.fa-react {
  color: lightblue;
  font-size: 16px;
}